            //     company_password:this.CompanyRegisterform.company_code
            // })
            //  this.$refs.upload.submit();
                  
            // setTimeout(function (){
            //   alert("ddd");
            // },"3000");
            this.$router.push({
                path:'/',
                query:{

                }
            });

        }else{
          alert("请先同意协议！");
        }
        },

        submitUpload() {
            this.$refs.upload.submit();
        },

        handleRemove(file, fileList) {
            console.log(file, fileList);
        },

        handlePreview(file) {
            console.log(file);
        },
    }
}
</script>
