<template>
    <div class="background">
        <v-app class="bidding">

            <div>
                <v-card>
                <v-container>
                <v-layout >
                    <v-flex xs12>
                        <span id="hello" :v-bind="company_name">&nbsp招标中的项目</span>
                    </v-flex>
                </v-layout>
                </v-container>
                </v-card>
                </div>            

                <div>
                <v-card>
                <v-container>
                <v-layout >
                    <v-flex xs12>
                        <el-breadcrumb separator-class="el-icon-arrow-right">
                            <el-breadcrumb-item :to="{ path: '/companyafterlogin/shouye', query:{company_id:this.$route.query.company_id,} }">我的公司</el-breadcrumb-item>
                            <el-breadcrumb-item :to="{ path: '/companyafterlogin/bidding', query:{company_id:this.$route.query.company_id,} }">招标中</el-breadcrumb-item>
                        </el-breadcrumb>                    
                    </v-flex>
                </v-layout>
                </v-container>
                </v-card>
                </div>
                
                <br>
                <el-row :gutter="30">
                    <div>
                    <v-card style="margin-left:2rem; margin-right:2rem; margin-top:2rem;">
                    <el-col :xs="24" :sm="24" :md="24" :lg="12" :xl="12" v-for="(item,index) in biddingprojects.slice((currentPage-1)*pagesize,currentPage*pagesize)" :key="item" :offset="0" >        
                        <v-card >
                            <v-container>
                                <v-layout>
                                    <v-flex xs3>
                                        <img src="../../assets/logo.png" class="image">

                                    </v-flex>

                                    <v-divider
                                        class="mx-3"
                                        inset
                                        vertical
                                    ></v-divider> 

                                    <v-flex xs9>

                                        <v-container>
                                            <v-layout>
                                                <v-flex xs7>
                                                    <div class="projectInfo">
                                                        <span><el-button type="text" @click="turnToProjectDetail(index)">{{item.name}}</el-button></span><br>
                                                        <span>项目编号：{{item.id}}</span><br>
                                                        <span>已投标数：{{item.num}}</span><br>
                                                    </div>
                                                </v-flex>
                                                <v-flex xs5>
                                                    <div style="text-align:right; font-size:26px; color:#34c484">
                                                        <span>竞标中</span><br>
                                                    <div class="bottom clearfix">
                                                        <v-btn left  class="button" @click="turntoChooseTender(index)">选择投标者</v-btn>
                                                    </div>    
                                                    </div>
                                                </v-flex>

                                            </v-layout>
                                        </v-container>

                                        </v-flex>
                                    </v-layout>
                                </v-container>

                            </v-card><br>
                        </el-col>
                        </v-card>
                    </div>
                    </el-row>

                    <div id="pagination">
                        <v-container>
                            <v-layout>
                                <v-flex xs3>&nbsp</v-flex>
                                <v-flex xs6>

                                    <el-pagination
                                        @size-change="handleSizeChange"
                                        @current-change="handleCurrentChange"
                                        :current-page="currentPage"
                                        :page-sizes="[4, 6 ,8, 10]" 
                                        :page-size="pagesize"         
                                        layout="total, sizes, prev, pager, next, jumper"
                                        :total="biddingprojects.length">   
                                    </el-pagination>  

                                </v-flex>
                            </v-layout>
                        </v-container>

                    </div>
        </v-app>
    </div>
</template>
<script>
export default {

    data(){
        return{

            //分页
            currentPage:1, //初始页
            pagesize:10,    //    每页的数据

            company_id:this.$route.query.company_id,
            company_name:this.$route.query.company_name,
            breadcrumbitems: [
                {
                    text: '公司管理',
                    disabled: false,
                   href: '/companyafterlogin/bidding'
                    },
                    {
                    text: '正在招标',
                    disabled: false,
                    href: '/companyafterlogin/bidding'
                    },
                    {
                    text: 'Link 2',
                    disabled: false,
                    href: 'breadcrumbs_link_2'
                    }
            ],
            biddingprojects:[
                {
                    id:1,
                    name:'xiangmu1',
                    num:10,
                    Pimage: "../../assets/pictures/p4.jpg",
                },
                {
                    id:2,
                    name:'xiangmu1',
                    num:1,
                    Pimage: "../../assets/pictures/p4.jpg",
                    
                },
                {
                    id:3,
                    name:'xiangmu1',
                    num:3,
                    Pimage: "../../assets/pictures/p4.jpg",

                },
                {
                    id:4,
                    name:'xiangmu1',
                    num:6,
                    Pimage: "../../assets/pictures/p4.jpg",

                },
                {
                    id:5,
                    name:'xiangmu1',
                    num:7,
                    Pimage: "../../assets/pictures/p4.jpg",

                }
            ]
        }
    },
    methods:{
        turntoChooseTender:function(index){
                this.$router.push({
                path:'/companyafterlogin/choose-tender',
                query:{
                     company_id:this.$route.query.company_id,               
                     biddingproject_id:this.biddingprojects[index].id,
                     biddingproject_name:this.biddingprojects[index].name,
                     biddingproject_num:this.biddingprojects[index].num,
                     
                }
            });
        },

        turnToProjectDetail:function(index){
                this.$router.push({
                path:'/companyafterlogin/projectdetail',
                query:{
                     company_id:this.$route.query.company_id,               
                     biddingproject_id:this.biddingprojects[index].id,
                     biddingproject_name:this.biddingprojects[index].name,
                     biddingproject_num:this.biddingprojects[index].num,
                     
                }
            });
        },

        //分页函数
        // 初始页currentPage、初始每页数据数pagesize和数据data
        handleSizeChange: function (size) {
                this.pagesize = size;
                console.log(this.pagesize)  //每页下拉显示数据
        },
        handleCurrentChange: function(currentPage){
                this.currentPage = currentPage;
                console.log(this.currentPage)  //点击第几页
        },



    },
    mounted(){

    }
    
}
</script>
<style>
/*你好 公司*/
#hello{
  font-size: 25px;
  color:slategray;
  /* color: #297fca; */
  text-align: left;
}
/*项目信息*/
.projectInfo{
    font-style: openSans;
    text-align:left;
    font-size:25px;
    color: #297fca;
    padding: 14px;
}
/*项目按钮*/
.BottomClearfix{
    font-family: "Open Sans";
}
/*分页pagination*/
#pagination{
    float:center;
}
.background{
    background-color:rgba(242,245,250,1);
}
.bidding{
   
    font-size:1.2rem;
    /* margin-left:5rem;
    margin-right:5rem;
    margin-top:2rem; */
    text-align: left;


}
.card1{
     border-color: rgba(0, 191, 255, 0);
     border-width: 0.5rem;
     border-radius: 10px;
     margin-left:6rem;
     margin-right:6rem;
}
/* .card1:hover{
    border-width: 2rem;
    border-color: rgba(0, 191, 255, 0.2);
} */

.image {
    max-width: 100%;
    height: auto;
}

  @media(max-width: 960px) {
    .bidding {
      width: 100%;
      margin: 0.5rem 0 0.5rem 0;
      font-size:15px;
      text-align:left;
    }
    .image {
        max-width: 80%;
        height: auto;
    }
    .card1{
     border-color: rgba(0, 191, 255, 0);
     border-width: 0rem;
     margin-left:0.1rem;
     margin-right:0.1rem;
    }
    .card1:hover{
    border-width: 0.2rem;
    border-color: rgba(2, 36, 231, 0.8);
    }
    }

</style>



