<template>
    <div>
        

            <div>
                <v-card>
                <v-container>
                <v-layout>
                    <v-flex xs12>
                        <span id="hello">&nbsp进行中的项目</span>
                    </v-flex>
                </v-layout>
                </v-container>
                </v-card>
            </div>

            <div>
                <v-card>
                    <v-container>
                        <v-layout >
                            <v-flex xs12>
                                <el-breadcrumb separator-class="el-icon-arrow-right">
                                <el-breadcrumb-item :to="{ path: '/companyafterlogin/shouye', query:{company_id:this.$route.query.company_id,} }">项目概览</el-breadcrumb-item>
                                <el-breadcrumb-item :to="{ path: '/companyafterlogin/executing' , query:{company_id:this.$route.query.company_id,}}">进行中项目总览</el-breadcrumb-item>
                                </el-breadcrumb>                    
                            </v-flex>
                        </v-layout>
                    </v-container>
            </v-card>
        </div>  

        <div class="companyexecuting">

                <el-row :gutter="30">

                    <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12" v-for="(item,index) in executingprojects.slice((currentPage-1)*pagesize,currentPage*pagesize)" :key="o" :offset="0" > 

                        <el-card  shadow="hover"style="border-radius:18px 18px 18px 18px;" class="companyexecutingcard">
                            
                            <v-container>

                                <v-layout>

                                    <v-flex xs4>
                                        <img src="../../assets/logo.png" class="image"  @click="turntoexecutingdetail(index)">
                                    </v-flex>
                                    
                                    <v-divider
                                        class="mx-3"
                                        inset
                                        vertical
                                    ></v-divider>

                                    <v-flex xs5>

                                        <div style="">
                                            <div ><el-button type="text" class="companyexecutingitemnamebutton" @click="turntoexecutingdetail(index)" size="mini">{{item.name}}</el-button></div><br>
                                                <span class="el-icon-edit" id="xiangmuxinxi"><span>编号：{{item.id}}</span></span><br>
                                                <span class="el-icon-time"><span id="xiangmuxinxi">发布：2017-6-8 12:30</span></span><br><br>
                                            <div style="box-shadow:#ff0000 0px 0px 3px;border-radius:5px;display: inline-block;"><i class="el-icon-bell" style="color:#FF3300"><span>{{item.state}}</span></i></div>

                                            <br>
                                            <v-btn color="success"  @click="turntoexecutingdetail(index)">查看进度</v-btn>

                                        </div>
                                                                                       
                                    </v-flex>

                                    <v-flex xs3>
                                        <div class="executingprogress">
                                            <el-progress type="circle" :percentage="percentage" color="	#FF8C00" stroke-width=10 width=100></el-progress>
                                            <el-button type="text" class="fontbutton1">当前进度(2/5)</el-button>
                                        </div>                                            
                                    </v-flex> 

                                </v-layout>

                            </v-container>


                    </el-card><br>
 
                </el-col>
            </el-row>

                <el-row>
                    
                    <el-col :span="7">&nbsp</el-col>

                    <el-col :span="6">
                            <div>
                                    <el-pagination
                                        @size-change="handleSizeChange"
                                        @current-change="handleCurrentChange"
                                        :current-page="currentPage"
                                        :page-sizes="[4, 6 ,8, 10]" 
                                        :page-size="pagesize"         
                                        layout="total, sizes, prev, pager, next, jumper"
                                        :total="executingprojects.length">   
                                </el-pagination>

                            </div>
                    </el-col>

                </el-row>

        </div>
    </div>
</template>
<script>
export default {

    data(){
        return{
            percentage:50,
            //分页
            currentPage:1, //初始页
            pagesize:10,    //    每页的数据

            executingprojects:[
                {
                    id:11,
                    name:'xiangmu1',
                    state:'待审核中',
                },
                {
                    id:11,
                    name:'xiangmu1',
                    state:'待审核中',
                },
                {
                    id:12,
                    name:'xiangmu1',
                    state:'上次审核通过',
                },
                {
                    id:13,
                    name:'xiangmu1',
                    state:'上次审核不通过',
                },
                {
                    id:14,
                    name:'xiangmu1',
                    state:'仲裁中，等待专家处理',
                },
                {
                    id:15,
                    name:'xiangmu1',
                    state:'已仲裁，等待您处理',
                },
                {
                    id:16,
                    name:'xiangmu16',
                    state:'同意乙方继续',
                },
                {
                    id:17,
                    name:'xiangmu16',
                    state:'终止合同',
                },
                {
                    id:18,
                    name:'xiangmu16',
                    state:'仲裁乙方胜，请支付该进度进度款',
                },
            ]
        }
    },
    methods:{

        turntoexecutingdetail:function(index){
            this.$router.push({
                 path:'/companyafterlogin/executingdetail',
                 query:{
                     company_id:this.$route.query.company_id,
                     executingproject_id:this.executingprojects[index].id,
                     executingproject_state:this.executingprojects[index].state,
                    }

                })
        },

        //分页函数
        // 初始页currentPage、初始每页数据数pagesize和数据data
        handleSizeChange: function (size) {
                this.pagesize = size;
                console.log(this.pagesize)  //每页下拉显示数据
        },
        handleCurrentChange: function(currentPage){
                this.currentPage = currentPage;
                console.log(this.currentPage)  //点击第几页
        },

    },
    mounted(){

    }
    
}
</script>
<style>
/*你好 公司*/
#hello{
  font-size: 25px;
  color:slategray;
  /* color: #297fca; */
  text-align: left;
}
/*项目信息*/
#xiangmuxinxi{
    font-family: "Open Sans";
    font-size: 18px;
    color:#297fca;
    text-align: left;
}
.companyexecuting{
   
    font-size:1.2rem;
    margin-left:5rem;
    margin-right:5rem;
    margin-top:2rem;
    text-align: left;
    background-color: rgba(25,65,233,0);

}
.clear{
    clear: both;
}
.image {
    max-width: 100%;
    height: auto;
}
.companyexecutingitemnamebutton{
    font-size: 24px;
    color: #4186dc;
}
.executingprogress{
    margin-top:45px;
    margin-left: 20px;
}
  @media(max-width: 740px) {
    .companyexecuting {
      width: 100%;
      margin: 0.4rem 0 0.4rem 0;
      font-size:14px;
      text-align: left;
    }
    .image {
        max-width: 80%;
        height: auto;
        margin-top:20px;
    }
    .companyexecutingitemnamebutton{
    font-size: 30px;
    color: #4186dc;
    }
    .fontbutton1{
        font-size: 14px;
    }
    .executingprogress{
    margin-top:10px;
    margin-left:5px;
    }
    /*项目信息*/
    #xiangmuxinxi{
        font-family: "Open Sans";
        font-size: 14px;
        color:#297fca;
        text-align: left;
    }

    }
</style>



