<template>
    <div>
        <div class="studioended">
              <el-row>
                <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" v-for="(item,index) in endedprojects" :key="index" :offset="0"  @click="turntobiddingdetail(index)">        
                    <el-card  shadow="hover"style="border-radius:18px 18px 18px 18px;" class="studiobiddingcard">
                        <el-row>
                            <el-col :span="10">
                                   <div style="text-align:center" ><img src="../../assets/logo.png" class="image" @click="turntoendeddetail(index)"></div>
                            </el-col>
                            <el-col :span="14" @click="turntobiddingdetail(index)">
                                <div style="">
                                    <div style="float:left;"><el-button type="text"class="biddingitemname" @click="turntoendeddetail(index)">{{item.name}}</el-button></div>
                                    <div style="float:right;color:#34c484;"><p>已完成</p></div>
                                    <div class="clear"></div>
                                    <div style="float:left"><el-tag>项目外包</el-tag><el-tag>微信小程序</el-tag></div>                                       
                                    <div class="clear"></div>
                                    <div style="margin-top:10px;"><i class="el-icon-tickets" style="font-style:italic">项目编号:{{item.id}}</i></div>
                                    <div style="float:left;"><i class="el-icon-goods" style="font-style:italic">公司:{{item.id}}1111111&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp</i></div>
                                    <div style=""><i class="el-icon-location-outline" style="font-style:italic">城市:上海</i></div>     
                                    <div style="float:right;" class="time"><p><i class="el-icon-time">完成时间：<i>2017-6-8 12:30</i></i></p></div> 
                                </div>   
                            </el-col>
                        </el-row>
                    </el-card><br>
                </el-col>
            </el-row>
        </div>
    </div>   
</template>
<script>
export default {
    data(){
        return{
            endedprojects:[
                {
                    id:21,
                    name:'xiangmu1'
                },
                {
                    id:22,
                    name:'xiangmu1'
                },
                {
                    id:23,
                    name:'xiangmu1'
                },
                {
                    id:24,
                    name:'xiangmu1'
                },
                {
                    id:25,
                    name:'xiangmu1'
                }
            ]
        }
    },
    methods:{
        turntoendeddetail:function(index){
                this.$router.push({
                path:'/studioafterlogin/endeddetail',
                query:{
                     studio_id:this.$route.query.studio_id,               
                     endedproject_id:this.endedprojects[index].id
                }
            });
            
        }

    },
    mounted(){

    }
}
</script>
<style>
.clear{
    clear: both;
}
.studioended{
   
    font-size:1.2rem;
    margin-left:4rem;
    margin-right:4rem;
    margin-top:2rem;  
    border-radius:25px 25px 10px 10px;
}
.studiobiddingcard{
     border-color: rgba(0, 191, 255, 0);
     border-width: 0.5rem;
}
.studiobiddingcard:hover{
    border-width: 0.6rem;
    border-color: rgba(0, 191, 255, 0.2);
}
.image {
    max-width: 100%;
    height: auto;
    text-align: center;
}
.biddingitemname{
    font-size:30px;
    color:black;
    font-weight: bold;
}
.time{
    font-size: 18px;
}
  @media(max-width: 800px) {
    .studioended {
      width: 100%;
      margin: 0.5rem 0 0.2rem 0;
      font-size:18px;
      text-align: left;
    }
    .image {
        width: 80%;
        height: auto;
        margin-top: 20px;
    }
    .studiobiddingcard{
     border-color: rgba(0, 191, 255, 0);
     border-width: 0rem;
    }
    .studiobiddingcard:hover{
    border-width: 1px;
    border-color: rgba(2, 36, 231, 0.8);
    }
    .biddingitemname{
    font-size:18px;
    color:black;
    font-weight: bold;
}
.time{
    font-size: 16px;
    margin-top: 10px;
}
}

@media(max-width: 400px) {
    .studioended {
      width: 100%;
      margin: 0.5rem 0 0.2rem 0;
      font-size:14px;
      text-align: left;
    }
    .image {
        width: 80%;
        height: auto;
        margin-top: 20px;
    }
    .studiobiddingcard{
     border-color: rgba(0, 191, 255, 0);
     border-width: 0rem;
    }
    .studiobiddingcard:hover{
    border-width: 1px;
    border-color: rgba(2, 36, 231, 0.8);
    }
    .biddingitemname{
    font-size:18px;
    color:black;
    font-weight: bold;
}
.time{
    font-size: 12px;
      margin-top: 0px;
}
}

</style>


