                   studio_id:this.$route.query.studio_id,
                     code:this.$route.query.code
              }
          });
        //   this.$router.go(0);
      },
      turntodisputing:function(){
          this.$router.push({
              path:'/studioafterlogin/disputing',
              query:{
                   studio_id:this.$route.query.studio_id,
                     code:this.$route.query.code
              }
          });
        //   this.$router.go(0);
      },
