          this.$router.push({
              path:'/studioafterlogin/studiomanagement',
              query:{
                   studio_id:this.$route.query.studio_id,
                     code:this.$route.query.code
              }
          });
        //   this.$router.go(0);
      },
    turntodisputed(){
            this.$router.push({
                path:'/studioafterlogin/disputed',
