<template>
    <div>
     <!-- <div  style="margin-right:1rem;float:right">
         <el-badge :value="count" class="item">
                <el-button size="small" @click="turntodisputed">已解决纠纷</el-button>
         </el-badge>
    </div> -->
    <div class="studiodisputing">          
        <el-row>
            <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" v-for="(item,index) in disputingprojects" :key="o" :offset="0" >        
                <el-card  shadow="hover"style="border-radius:18px 18px 18px 18px;" class="studiodisputingcard">
                    <el-row>
                        <el-col :span="10">
                           <div style="text-align:center"><img src="../../assets/logo.png" class="image"  @click="turntodisputingdetail(index)"></div>            
                        </el-col>
                        <el-col :span="14">
                            <div style="">
                                <div style="float:left;"><el-button type="text" class="studiodisputingitemname" size="mini"  @click="turntodisputingdetail(index)">{{item.name}}</el-button></div>
                                <div class="clear"></div>
                                <div><i class="el-icon-edit" style=""><span style="color:black">编号：{{item.id}}</span></i></div>
                                <div style="float:left;"><i class="el-icon-goods" style="font-style:italic">公司:{{item.id}}1111111&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp</i></div>
                                <div class="clear"></div>
                                <div style="box-shadow:#ff0000 0px 0px 3px;border-radius:5px;display: inline-block;background-color:#df8585"><i class="el-icon-bell" style="color:white"><span>纠纷待解决</span></i></div>  
                                <div class="clear"></div>
                                <div style="float:right;" class="time1"><p><i class="el-icon-time"><i>2017-6-8 12:30</i></i></p></div> 
                            </div>   
                        </el-col>
                    </el-row>
             </el-card><br>
            </el-col>
          </el-row>
        </div>
    </div>
</template>
<script>
export default {

    data(){
        return{
            count:2,
            disputingprojects:[
                {
                    id:31,
                    name:'xiangmu1'
                },
                {
                    id:32,
                    name:'xiangmu1'
                },
                {
                    id:33,
                    name:'xiangmu1'
                },
                {
                    id:34,
                    name:'xiangmu1'
                },
                {
                    id:35,
                    name:'xiangmu1'
                }
            ]
        }
    },
    methods:{
        turntodisputingdetail:function(index){
                this.$router.push({
                path:'/studioafterlogin/disputingdetail',
                query:{
                     studio_id:this.$route.query.studio_id,               
                     disputingproject_id:this.disputingprojects[index].id
                }
            });
            
        },
        turntodisputed(){
            this.$router.push({
                path:'/studioafterlogin/disputed',
                query:{
                     studio_id:this.$route.query.studio_id,    
                }
            })
        }

    },
    mounted(){

    }
    
}
</script>
<style>
.studiodisputing{
   
    font-size:1.2rem;
    margin-left:4rem;
    margin-right:4rem;
    margin-top:2rem;
    text-align: left;
    border-color: rgba(0, 191, 255, 0);

}
.studiodisputingcard{
     border-color: rgba(0, 191, 255, 0);
     border-width: 0.5rem;
}
.studiodisputingcard:hover{
     border-color: rgba(0, 191, 255, 0.3);
     border-width: 0.5rem;
}

.image {
    max-width: 100%;
    height: auto;
}
.clear{
    clear: both;
}
.studiodisputingitemname{
    font-size:30px;
    color: #4186dc;
}
  @media(max-width: 650px) {
    .studiodisputing{
      width: 100%;
      margin: 0.5rem 0 0.5rem 0;
      font-size:15px;
      text-align: left;
    }
    .image {
        max-width: 80%;
        height: auto;
        margin-top:10px;
    }
    .studiodisputingitemname{
    font-size:18px;
    margin-top:-2rem;
    }
    .time1{
        margin-bottom: -2rem;
    }
    }

</style>



