<template>
    <div>
        <div>
            <v-card>
            <v-container>
            <v-layout >
                <v-flex xs12>
                    <span id="hello" >&nbsp已处理纠纷</span>
                </v-flex>
            </v-layout>
            </v-container>
            </v-card>
        </div>

        <div>
            <v-card>
            <v-container>
            <v-layout >
                <v-flex xs12>
                    <el-breadcrumb separator-class="el-icon-arrow-right">
                    <el-breadcrumb-item :to="{ path: '/companyafterlogin/shouye', query:{company_id:this.$route.query.company_id,} }">项目概览</el-breadcrumb-item>
                    <el-breadcrumb-item :to="{ path: '/companyafterlogin/disputing', query:{company_id:this.$route.query.company_id,} }">已处理的纠纷</el-breadcrumb-item>
                    </el-breadcrumb>                    
                </v-flex>
            </v-layout>
            </v-container>
            </v-card>
        </div>

    <div>

        <el-row :gutter="30">

            <div>

                <v-card style="margin-left:2rem; margin-right:2rem; margin-top:1rem;">

                    <el-col :xs="24" :sm="24" :md="24" :lg="12" :xl="12" v-for="(item,index) in disputingprojects.slice((currentPage-1)*pagesize,currentPage*pagesize)" :key="o" :offset="0" >
                        <v-card>
                            <v-container>
                                <v-layout>
                                    <v-flex xs3>
                                        <img src="../../assets/logo.png" class="image">
                                    </v-flex>

                                    <v-divider
                                        class="mx-3"
                                        inset
                                        vertical
                                    ></v-divider>

                                    <v-flex xs9>

                                        <v-container class="biddingprojectInfo">
                                            <v-layout>
                                                <v-flex xs7>
                                                    <span>项目名：{{item.name}}</span><br> 
                                                    <span>项目编号：{{item.id}}</span><br>                              
                                                </v-flex>
                                                <v-flex xs5>
                                                        <span>已处理项目</span><br>
                                                        <div class="bottom clearfix">
                                                            <v-btn left  class="button" @click="turntodisputingdetail(index)">详&nbsp情</v-btn>                                    
                                                        </div>                                                                    
                                                </v-flex>
                                            </v-layout>
                                        </v-container>

                                    </v-flex>
                                </v-layout>
                            </v-container>
                        </v-card><br>

                    </el-col>
                </v-card>

            </div>

        </el-row>

        <div id="pagination">
            <v-container>
                <v-layout>
                    <v-flex xs3>&nbsp</v-flex>
                    <v-flex xs6>

                        <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-sizes="[4, 6 ,8, 10]" 
                            :page-size="pagesize"         
                            layout="total, sizes, prev, pager, next, jumper"
                            :total="disputingprojects.length">   
                        </el-pagination>  

                    </v-flex>
                </v-layout>
            </v-container>

        </div>        

    </div>


    </div>
</template>
<script>
export default {

    data(){
        return{

            //分页
            currentPage:1, //初始页
            pagesize:10,    //    每页的数据

            count:2,
            disputingprojects:[
                {
                    id:31,
                    name:'xiangmu1'
                },
                {
                    id:32,
                    name:'xiangmu1'
                },
                {
                    id:33,
                    name:'xiangmu1'
                },
                {
                    id:34,
                    name:'xiangmu1'
                },
                {
                    id:35,
                    name:'xiangmu1'
                }
            ]
        }
    },
    methods:{

        turntodisputingdetail:function(index){
                this.$router.push({
                path:'/companyafterlogin/disputingdetail',
                query:{
                     company_id:this.$route.query.company_id,               
                     disputingproject_id:this.disputingprojects[index].id
                }
            });
            
        },

                // 初始页currentPage、初始每页数据数pagesize和数据data
        handleSizeChange: function (size) {
                this.pagesize = size;
                console.log(this.pagesize)  //每页下拉显示数据
        },
        handleCurrentChange: function(currentPage){
                this.currentPage = currentPage;
                console.log(this.currentPage)  //点击第几页
        },

    },
    mounted(){

    }
    
}
</script>
<style>
/*你好 公司*/
#hello{
  font-size: 25px;
  color:slategray;
  /* color: #297fca; */
  text-align: left;
}
/*纠纷项目信息*/
.biddingprojectInfo{
    font-style: openSans;
    text-align:left;
    font-size:25px;
    color: #297fca;
    padding: 14px;
}
.f{
   
    font-size:1.2rem;
    margin-left:5rem;
    margin-right:5rem;
    margin-top:2rem;
    text-align: left;

}
.card1{
     border-color: rgba(0, 191, 255, 0);
     border-width: 0.5rem;
     border-radius: 10px;
     margin-left:6rem;
     margin-right:6rem;
}

.image {
    max-width: 100%;
    height: auto;
}

  @media(max-width: 960px) {
    .f {
      width: 100%;
      margin: 0.5rem 0 0.5rem 0;
      font-size:15px;
      text-align: left;
    }
    .image {
        max-width: 80%;
        height: auto;
    }
    .card1{
     border-color: rgba(0, 191, 255, 0);
     border-width: 0rem;
     margin-left:0.1rem;
     margin-right:0.1rem;
    }    
}

</style>



