<template>
    <div id='app'>

    <div>
      <v-card>
        <v-container>
            <v-layout >
                <v-flex xs12>
                    <span id="hello" :v-bind="company_name">&nbsp发布您的需求</span>
                </v-flex>
            </v-layout>
    </v-container>
      </v-card>
    </div>

    <div>
        <v-card>
            <v-container>
            <v-layout >
                <v-flex xs12>
                    <el-breadcrumb separator-class="el-icon-arrow-right">
                    <el-breadcrumb-item :to="{ path: '/companyafterlogin/shouye', query:{company_id:this.$route.query.company_id,} }">项目概览</el-breadcrumb-item>
                    <el-breadcrumb-item :to="{ path: '/companyafterlogin/postProject', query:{company_id:this.$route.query.company_id,} }">发布需求</el-breadcrumb-item>
                    </el-breadcrumb>                    
                </v-flex>
            </v-layout>
            </v-container>
        </v-card>
    </div>

      <div class='f'>
        <el-card>

            <v-container>
             <v-layout>
               <v-flex xs4><hr style="background-color:#00c1df"></v-flex>
               <v-flex xs4><span id="xiangmushangding">&nbsp&nbsp&nbsp&nbsp发布需求</span></v-flex>
               <v-flex xs4><hr style="background-color:#00c1df"></v-flex>
             </v-layout>
           </v-container>

          <el-row>
              <el-col  :xs="0" :sm="2" :md="2" :lg="1" :xl="1"><div class="grid-content bg-purple"></div>&nbsp</el-col>
              
              <el-col  :xs="18" :sm="12" :md="12" :lg="9" :xl="9">
                  <div class="grid-content bg-purple-light">
                    <el-form ref="project" :model="project" label-width="100px">
                        <br><br>
                        <el-form-item label="项目名">
                            <el-input v-model="project.name" placeholder="请填写项目名" prefix-icon="el-icon-edit"></el-input>
                        </el-form-item>
                        <el-form-item label="参考报价">
                            <el-input v-model="project.quote" placeholder="请填写参考报价(元)" prefix-icon="el-icon-edit"></el-input>
                        </el-form-item>
                        <el-row>
                            <el-col :xs="0" :sm="3" :md="4" :lg="8" :xl="8"></el-col>
                            <el-col :xs="16" :sm="18" :md="16" :lg="20" :xl="20">
                                <el-form-item label="技术要求">
                                    <el-select v-model="demand" multiple placeholder="请选择技术要求">
                                    <el-option
                                        v-for="item in techOption"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                                </el-form-item>
                            </el-col>
                            <el-col :xs="8" :sm="3" :md="4" :lg="4" :xl="4"></el-col>
                        </el-row>
                        <el-row>
                            <el-col :xs="0" :sm="3" :md="4" :lg="8" :xl="8"></el-col>
                            <el-col :xs="16" :sm="18" :md="16" :lg="20" :xl="20">
                                <el-form-item label="外包类型">
                                    <el-select v-model="typedemand" placeholder="请选择外包类型">
                                        <el-option
                                            v-for="item in type1"
                                            :key="item.value"
                                            :label="item.label"
                                            :value="item.value">
                                        </el-option>
                                    </el-select>
                                    </el-form-item>
                            </el-col>
                            <el-col :xs="8" :sm="3" :md="4" :lg="4" :xl="4"></el-col>
                        </el-row>
                        <el-row>
                            <el-col :xs="0" :sm="0" :md="0" :lg="0" :xl="0"></el-col>
                            <el-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                                <el-form-item label="项目图片">
                                    <el-upload
                                        class="upload-demo"
                                        action="https://jsonplaceholder.typicode.com/posts/"
                                            :on-preview="handlePreview"
                                            :on-remove="handleRemove"
                                            :file-list="fileList1"
                                            list-type="picture">
                                        <v-btn color="primary" style="margin-bottom:10px" small @click="">点击上传</v-btn>
                                    </el-upload>
                                </el-form-item>
                            </el-col>
                        </el-row>

                        <el-row>
                            <el-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                                <el-form-item label="招标书">
                                    <el-upload
                                        class="upload-demo"
                                        action="https://jsonplaceholder.typicode.com/posts/"
                                        :on-preview="handlePreview1"
                                        :on-remove="handleRemove1"
                                        :file-list="fileList2"
                                        list-type="picture">
                                    <v-btn color="primary" style="margin-bottom:10px" small @click="">添加附件</v-btn>
                                    </el-upload>
                                </el-form-item>
                            </el-col>
                            <el-col :xs="2" :sm="2" :md="2" :lg="2" :xl="2">&nbsp&nbsp</el-col>
                            <el-col :xs="10" :sm="10" :md="10" :lg="10">
                                <p class="supportFormat">&nbsp（支持文件格式：JPG，PNG，GIF，DOC，DOCX，RAR，ZIP，PDF）</p>
                            </el-col>
                        </el-row>

                   </el-form>
                  </div></el-col>

                <el-col :xs="1" :sm="1" :md="1" :lg="2" :xl="2">&nbsp</el-col>

                <el-col :xs="16" :sm="9" :md="9" :lg="10" :xl="10">
                    <br><br>
                    <el-form>
                        <el-form-item label="项目简述">
                                <el-input type="textarea" placeholder="项目简述:" v-model="project.info" :autosize="{ minRows: 10, maxRows: 22}" prefix-icon="el-icon-info"></el-input> 
                        </el-form-item>                                        
                    </el-form>

                    <el-row>
                    <el-col :span="15">
                        <el-form>
                            <el-form-item label="验证码">
                                <el-input v-model="project.checkCode" style="color:blue;width:100%" prefix-icon="el-icon-mobile-phone" placeholder="请输入手机验证码"></el-input>
                            </el-form-item>
                        </el-form>
                    </el-col>
                    <el-col :span="2">
                        <br><br>
                        <v-btn color="primary" style="margin-bottom:10px" small @click="">获取</v-btn>         
                    </el-col>
                    <el-col :span="8">          
                    </el-col>
                    </el-row>

                </el-col>
          </el-row>

          <el-row>
              <el-col  :xs="0" :sm="2" :md="2" :lg="2" :xl="2"><div class="grid-content bg-purple"></div>&nbsp</el-col>
              <el-col :xs="16" :sm="18" :md="18" :lg="18" :xl="18">
                  <el-form>
                    <el-form-item label="">
                                <el-input type="textarea" placeholder="其他事项：" v-model="project.desc" :autosize="{ minRows: 4, maxRows: 8}"></el-input> 
                    </el-form-item>
                  </el-form>
              </el-col>

          </el-row>

            <v-container>
                <v-layout>
                    <v-flex xs4>&nbsp</v-flex>
                    <v-flex xs6>
                        <el-button type="success" style="background-color:#33546d;color:white" plain>发布您的需求</el-button>
                    </v-flex>
                </v-layout>
            </v-container>

        </el-card>  
      </div><br>  
    </div>
</template>
<script>
import axios from 'axios'
export default {
    name:'postProject',
    data(){
        return{
            demand: [],//技术要求
            typedemand: [],//外包类型要求
            fileList1: [],
            fileList2: [],
            project:{
                name:'',
                quote:'',
                desc:'',
                info:'',
                checkCode:'',
            },
            techOption:[
                {
                    value:'需求1',
                    label:'HTML'
                },
                {
                    value:'需求2',
                    label:'PHP'
                },
                {
                    value:'需求3',
                    label:'大数据'
                },
                {
                    value:'需求4',
                    label:'深度学习'
                },
            ],
            type1:[
                 {
                    value:'类型1',
                    label:'项目外包'
                },
                {
                    value:'类型2',
                    label:'人力外包'
                },
            ]
        }
    },
    methods:{
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
       handleRemove1(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview1(file) {
        console.log(file);
      },
    },
    components:{

    },
      mounted () {
      axios.get('http://localhost:8081/readers', {      //传
      
    })
    .then(function (response) {
      console.log(response);
      this.info=response;
    })
    .catch(function (error) {
      console.log(error);
    }); 
    }
}
</script>
<style>
/*你好 公司*/
#hello{
  font-size: 25px;
  color:slategray;
  /* color: #297fca; */
  text-align: left;
}
/*<p>（支持文件格式：JPG，PNG，GIF，DOC，DOCX，RAR，ZIP，PDF）</p>*/
.supportFormat{
    font-size: 10px;
    color:gray;
    text-align: left;
}
/*项目商定*/
#xiangmushangding{
  font-family: "Open Sans";
  font-size:40px;
  color: #00c1df;
  text-align: center;
}
.f{
   
    font-size:1.2rem;
    margin-left:4rem;
    margin-right:6rem;
    margin-top:2rem;
    text-align: left;
    border-radius: 25px;
    background-color: white
}    
@media (max-width: 960px) {
    .f{
        margin:0;
    }
    /*<p>（支持文件格式：JPG，PNG，GIF，DOC，DOCX，RAR，ZIP，PDF）</p>*/
    .supportFormat{
        font-size: 5px;
        text-align: right;
        color:gray;
    }
    /*项目商定*/
    #xiangmushangding{
      font-family: "Open Sans";
      font-size:30px;
      color: #00c1df;
      text-align: center;
    }
    
}
</style>



