                            <el-breadcrumb-item :to="{ path: '/companyafterlogin/bidding', query:{company_id:this.$route.query.company_id,} }">招标中</el-breadcrumb-item>
                            <el-breadcrumb-item :to="{ path: '/companyafterlogin/projectdetail', query:{company_id:this.$route.query.company_id,} }">项目详情</el-breadcrumb-item>
                        </el-breadcrumb>                    
