<script>
export default {
    data() {
      return {
           postproject_id:this.$route.query.postproject_id,
           company_id:this.$route.query.company_id,
           img1:require('../../assets/logo.png'),
           info:null,

            studioMessage:[
                {
                    info:'湖人目前只有28胜27负排在西部第10位，但他们距离西部第6位的爵士只差3个胜场。交易截止日前湖人本想换来安东尼-戴维斯，但最终以失败告终。湖人通过交易换来了布洛克和穆斯卡拉，这两人都有出色投射能力。佩林卡将两个新援跟超级碗MVP朱利安-埃德尔曼对比，他认为，爱国者是在埃德尔曼结束禁赛回归后开始进入节奏，他希望湖人也能在引援过后找到节奏。佩林卡说道：“我希望他们能像埃德尔曼那样，一个球员就能对球队整体的化学反应产生影响，我希望这两个球员来了后也能有那样的影响力。去年夏天，湖人在引援时重组织者轻射手，他们是本赛季三分火力最弱的球队之一。在截止日前，湖人连续引进射手，这是否代表湖人在纠正休赛期时的错误？佩林卡表示：“我并不认为球队这样做是全盘推翻之前的策略，我们的风格就是囤积组织者，我觉得可能算是调整吧。',
                },
              ]
      };
    },

    methods:{
           
    },

    mounted () {
      axios.get('http://localhost:8081/readers', {      //传
      id:this.$route.query.biddingproject_id
      
    })
    .then(function (response) {
      console.log(response);
      this.info=response;
    })
    .catch(function (error) {
      console.log(error);
    }); 
    }
}
</script>
<style>
/*你好 公司*/
#hello{
