        font-size:20px;
        color:#297fca;
        text-align:left;
    }
    /*招标中*/
    #zhaobiaozhong{
      font-family: "Open Sans";
      font-size: 20px;
      color:#58cb92;
      text-align: left;
    }
    /*项目简介*/
    #xiangmujianjie{
      font-family: "Open Sans";
      font-size: 28px;
      color:#75b7ff;
      text-align: center;
    }


    }
</style>



