<template>
  <div id="shouye">
  <!-- <el-carousel :interval="2000" arrow="always"   height="500px" >
      <el-carousel-item v-for="item in 3" :key="item">
      </el-carousel-item>
    </el-carousel> -->
    <div>
      <v-card>
    <v-container>
      <v-layout >
        <v-flex xs12>
            <span id="hello" :v-bind="company_name">&nbsp您好,{{company_name}}公司！这是您的项目概况。</span>
        </v-flex>
      </v-layout>
    </v-container>
      </v-card>
    </div>
<div id="app">
  <v-container>
    <v-layout row wrap>

      <v-flex xs5>
        <!--项目进度表-->
         <div>
      <v-data-table
        :headers="headers"
        :items="brands"
        :pagination.sync="pagination"
        :total-items="totalBrands"
        :loading="loading"
        class="elevation-1"
      >
        <template slot="items" slot-scope="props">
 
          <td class="text-xs-center">{{ props.item.id }}</td>
          <td class="text-xs-center">{{ props.item.name }}</td>
          <td class="text-xs-center"><img :src="props.item.image" alt=""/></td>
          <td class="text-xs-center">{{ props.item.letter }}</td>
          <td class="text-xs-center">修改/刪除</td>
        </template>
      </v-data-table>
    </div>
      </v-flex>
      <v-spacer></v-spacer>
      <v-flex xs6>
        <!--日历时间表-->
 <v-layout>
    <v-flex>
      <v-sheet height="500">
        <v-calendar
          :now="today"
          :value="today"
          color="primary"
        >
          <template v-slot:day="{ date }">
            <template v-for="event in eventsMap[date]">
              <v-menu
                :key="event.title"
                v-model="event.open"
                full-width
                offset-x
              >
                <template v-slot:activator="{ on }">
                  <div
                    v-if="!event.time"
                    v-ripple
                    class="my-event"
                    v-on="on"
                    v-html="event.title"
                  ></div>
                </template>
                <v-card
                  color="grey lighten-4"
                  min-width="350px"
                  flat
                >
                  <v-toolbar
                    color="primary"
                    dark
                  >
                    <v-btn icon>
                      <v-icon>edit</v-icon>
                    </v-btn>
                    <v-toolbar-title v-html="event.title"></v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-btn icon>
                      <v-icon>favorite</v-icon>
                    </v-btn>
                    <v-btn icon>
                      <v-icon>more_vert</v-icon>
                    </v-btn>
                  </v-toolbar>
                  <v-card-title primary-title>
                    <span v-html="event.details"></span>
                  </v-card-title>
                  <v-card-actions>
                    <v-btn
                      flat
                      color="secondary"
                    >
                      Cancel
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-menu>
            </template>
          </template>
        </v-calendar>
      </v-sheet>
    </v-flex>
  </v-layout> 
      </v-flex>           
              <!-- <v-flex
                xs12
                class="mb-3"
              >
                <v-sheet height="500">
                  <v-calendar
                    ref="calendar"
                    v-model="start"
                    :type="type"
                    :end="end"
                    color="primary"
                  ></v-calendar>
                </v-sheet>
              </v-flex> -->
          
              <!-- <v-flex
                sm4
                xs12
                class="text-sm-left text-xs-center"
              >
                <v-btn @click="$refs.calendar.prev()">
                  <v-icon
                    dark
                    left
                  >
                    keyboard_arrow_left
                  </v-icon>
                  Prev
                </v-btn>
              </v-flex>
              <v-flex
                sm4
                xs12
                class="text-xs-center"
              >
                <v-select
                  v-model="type"
                  :items="typeOptions"
                  label="Type"
                ></v-select>
              </v-flex>
              <v-flex
                sm4
                xs12
                class="text-sm-right text-xs-center"
              >
                <v-btn @click="$refs.calendar.next()">
                  Next
                  <v-icon
                    right
                    dark
                  >
                    keyboard_arrow_right
                  </v-icon>
                </v-btn>
              </v-flex> -->
            </v-layout>
          </e-card>
        </v-app>

      </v-flex>      
    </v-layout>
  </v-container>

</div>
  <br><br><br><br>
  </div>
</template>
<script>
export default {
    name:'company_shouye',
    data(){
      return{
          company_id:this.$route.query.company_id,
          company_name:this.$route.query.company_name,
          //日历
      today: '2019-01-08',
      events: [
        {
          title: 'Vacation',
          details: 'Going to the beach!',
          date: '2018-12-30',
          open: false
        },
        {
          title: 'Vacation',
          details: 'Going to the beach!',
          date: '2018-12-31',
          open: false
        },
        {
          title: 'Vacation',
          details: 'Going to the beach!',
          date: '2019-01-01',
          open: false
        },
        {
          title: 'Meeting',
          details: 'Spending time on how we do not have enough time',
          date: '2019-01-07',
          open: false
        },
        {
          title: '30th Birthday',
          details: 'Celebrate responsibly',
          date: '2019-01-03',
          open: false
        },
        {
          title: 'New Year',
          details: 'Eat chocolate until you pass out',
          date: '2019-01-01',
          open: false
        },
        {
          title: 'Conference',
          details: 'Mute myself the whole time and wonder why I am on this call',
          date: '2019-01-21',
          open: false
        },
        {
          title: 'Hackathon',
          details: 'Code like there is no tommorrow',
          date: '2019-02-01',
          open: false
        }
      ],
          //项目进度表
          
          totalBrands: 0,//
          brands: [],//数组，就是个数组，对应组件中的item
          loading: false,
          pagination: {},//空對象
          headers: [ //headers表头数组，里面是对象
            {
              text: '品牌id',//显示文本
              align: 'center',//left，center，rights三种对其方式
              sortable: true,//是不是需要排序
              value: 'id' //value这个头关联的字段
            },
            {text: '品牌名称', align: 'center', value: 'name' , sortable: false },
            {text: '品牌Logo', align: 'center', value: 'image' , sortable: false },
            {text: '首字母', align: 'center', value: 'letter' , sortable: false },
            {text: '操作', align: 'center', sortable: false }
 
          ],
          // typeOptions: [
          //   { text: 'Day', value: 'day' },
          //   { text: '4 Day', value: '4day' },
          //   { text: 'Week', value: 'week' },
          //   { text: 'Month', value: 'month' },
          //   { text: 'Custom Daily', value: 'custom-daily' },
          //   { text: 'Custom Weekly', value: 'custom-weekly' }
          // ],
      }
    },
    computed: {
      // convert the list of events into a map of lists keyed by date
      eventsMap () {
        const map = {}
        this.events.forEach(e => (map[e.date] = map[e.date] || []).push(e))
        return map
      }
    },    
    props: {
      source: String
    },

    methods:{

          handleOpen(key, keyPath) {
            console.log(key, keyPath);
          },
          handleClose(key, keyPath) {
            console.log(key, keyPath);
          },

          showIntervalLabel (interval) {
              return interval.minute === 0
          },
      open (event) {
        alert(event.title)
      }         
    },
    created(){
               this.brands=[
            {id:1,name:"小米",image:"",letter:"X"},
            {id:2,name:"華為",image:"",letter:"H"},
            {id:1,name:"Apple",image:"",letter:"A"},
            {id:1,name:"三星",image:"",letter:"S"},
            {id:4,name:"三星",image:"",letter:"S"},
            {id:5,name:"三星",image:"",letter:"S"},
          ];
          this.totalBrands=15;
    }
}
</script>
<style>
/*你好 公司*/
#hello{
  font-size: 25px;
  color:slategray;
  /* color: #297fca; */
  text-align: left;
}
  .el-carousel__item h {
    color: #475669;
    font-size: 18px;
    opacity: 0.75;
    line-height: 300px;
    margin: 0;
  }
  
  .el-carousel__item:nth-child(n) {
    background:url(../../assets/logo.png);
    background-position:center;
    background-repeat:no-repeat;
     background-color:#33CC00;
  }
  
  .el-carousel__item:nth-child(2n) {
    background:url(../../assets/logo2.png);
    background-position:center;
    background-repeat:no-repeat;
    background-color: #00CCFF;
  }
  .el-carousel__item:nth-child(3n) {
    background:url(../../assets/logo2.png);
    background-position:center;
    background-repeat:no-repeat;
    background-color: #FFFF99;
  }
  .img-responsive {
  display: inline-block;
  height: auto;
  max-width: 100%;
}
/*日历*/
 .my-event {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    border-radius: 2px;
    background-color: #1867c0;
    color: #ffffff;
    border: 1px solid #1867c0;
    width: 100%;
    font-size: 12px;
    padding: 3px;
    cursor: pointer;
    margin-bottom: 1px;
  }

</style>


