    methods:{
        submitUpload() {
        this.$refs.upload.submit();
      },
        StudioRegister(){

        if(this.StudioRegisterform.isagree==true)
        {
            this.$refs.upload.submit();
            // axios.post('http://localhost:8080/readers/',{           
            //     company_username:this.CompanyRegisterform.company_username,
            //     company_password:this.CompanyRegisterform.company_code
            // })
            //  this.$refs.upload.submit();
                  
            // setTimeout(function (){
            //   alert("ddd");
            // },"3000");
            this.$router.push({
                path:'/',
                query:{

                }
            });

        }else{
          alert("请先同意协议！");
        }
      },
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
        enterNextRegisterPage(){
          this.seen1=false;
          this.seen2=true;
      },
      enterPreviousRegisterPage(){
          this.seen1=true;
          this.seen2=false;
      },
       handleItemChange(val) {
        console.log('active item:', val);
        setTimeout(_ => {
          if (val.indexOf('江苏') > -1 && !this.options2[0].cities.length) {
            this.options2[0].cities = [{
              label: '南京'
            }];
          } else if (val.indexOf('浙江') > -1 && !this.options2[1].cities.length) {
            this.options2[1].cities = [{
              label: '杭州'
            }];
          }
        }, 300);
      },
      
  }
}
</script>
