<script>

  export default {
    name:'app',
    inject:['reload'],
    data() {
      return {
            id:this.$route.query.company_id,
            active: false,
            show:-1,
            content:null,
            dialog: false,
            drawer: null,
         items: [
        { icon: 'contacts', text: 'Contacts' },
        { icon: 'history', text: 'Frequently contacted' },
        { icon: 'content_copy', text: 'Duplicates' },
        {
          icon: 'keyboard_arrow_up',
          'icon-alt': 'keyboard_arrow_down',
          text: 'Labels',
          model: true,
          children: [
            { icon: 'add', text: 'Create label' }
          ]
        },
        {
          icon: 'keyboard_arrow_up',
          'icon-alt': 'keyboard_arrow_down',
          text: 'More',
          model: false,
          children: [
            { text: 'Import' },
            { text: 'Export' },
            { text: 'Print' },
            { text: 'Undo changes' },
            { text: 'Other contacts' }
          ]
        },
        { icon: 'settings', text: 'Settings' },
        { icon: 'chat_bubble', text: 'Send feedback' },
        { icon: 'help', text: 'Help' },
        { icon: 'phonelink', text: 'App downloads' },
        { icon: 'keyboard', text: 'Go to the old version' }
      ]            
      };
    },
        props: {
      source: String
    },
    components:{
    
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
      turntologin:function(){
          this.$router.push('/login');
         
         
      },
       turntoshouye:function(){
          this.$router.push({
                path:'/companyafterlogin/shouye',
                query:{
                     company_id:this.$route.query.company_id,
                     code:this.$route.query.code,
                }
            });
            
      },
      turntoproject:function(){
         this.$router.push({
                path:'/companyafterlogin/postProject',
                query:{
                     company_id:this.$route.query.company_id,
                     code:this.$route.query.code,
                }
            });
          
      },
       turntostudio:function(){
          this.$router.push({
                path:'/companyafterlogin/studio',
                query:{
                     company_id:this.$route.query.company_id,
                     code:this.$route.query.code,
                }
            });
            
      },
      turntobidding:function(){
          this.$router.push({
                path:'/companyafterlogin/bidding',
                query:{
                     company_id:this.$route.query.company_id,
                     code:this.$route.query.code,
                }
            });
            
      },
      turntoexecuting:function(){
          this.$router.push({
                path:'/companyafterlogin/executing',
                query:{
                     company_id:this.$route.query.company_id,
                     code:this.$route.query.code,
                }
            });
             
      },
      turntoended:function(){
          this.$router.push({
              path:'/companyafterlogin/ended',
              query:{
                   company_id:this.$route.query.company_id,
                     code:this.$route.query.code
              }
          });
          
      },
      turntodisputing:function(){
          this.$router.push({
              path:'/companyafterlogin/disputing',
              query:{
                   company_id:this.$route.query.company_id,
                     code:this.$route.query.code
              }
          });
          
      },
      turnToDisputingFinished:function(){
            this.$router.push({
              path:'/companyafterlogin/disputingFinished',
              query:{
                   company_id:this.$route.query.company_id,
                    code:this.$route.query.code
              }
          });
      },
     turnToaboutUs:function(){
          this.$router.push({
              path:'/companyafterlogin/aboutus',
              query:{
                   company_id:this.$route.query.company_id,
                    code:this.$route.query.code
              }
          });
          
      },
      turnToManagement:function(){
          this.$router.push({
              path:'/companyafterlogin/companymanagement',
              query:{
                   company_id:this.$route.query.company_id,
                    code:this.$route.query.code
              }
          });
          
      },
      
 
    }
  }
 
 
</script>
<style>
#nav1{
