               },
           ],
           info:null
       }
   },
   components:{
       'footer2':footer2,
   },
   methods:{
       todetails:function(index){
           this.$router.push({
                path:'/studioafterlogin/projectdetail',
                query:{
                     studio_id:this.$route.query.studio_id,               
                     project_id:this.projects[index].id
                }
            });
            
       }
   },
   mounted () {
      axios.get('http://localhost:8081/readers', {      //传
      
    })
