<template>
    <div><br>
        <div class="studiodisputed">          
             <el-row>
                <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" v-for="(item,index) in disputedprojects" :key="o" :offset="0" >        
                    <el-card  shadow="hover"style="border-radius:18px 18px 18px 18px;" class="studiodisputedcard">
                        <el-row>
                            <el-col :span="12">
                               <div style="text-align:center"> <img src="../../assets/logo.png" class="studiodisputedimage" @click="turntodisputeddetail(index)"></div>
                            </el-col>
                            <el-col :span="12">
                                <div style="">
                                    <div style="float:left;"><el-button type="text" class="studiodisputeditemname" size="mini"  @click="turntodisputeddetail(index)">{{item.name}}</el-button></div>
                                    <div class="clear"></div>
                                    <div><i class="el-icon-edit" style="float:left"><span style="color:black">编号：{{item.id}}</span></i></div>
                                    <div class="clear"></div>
                                    <div style="float:left;"><i class="el-icon-goods" style="font-style:italic">公司:{{item.id}}11111111&nbsp&nbsp&nbsp</i></div>
                                    <!-- <div style="float:left;"><i class="el-icon-goods" style="font-style:italic">城市:南京</i></div> -->
                                    <div class="clear"></div><br>
                                    <div style="box-shadow:#89c997 0px 0px 3px;border-radius:5px;display: inline-block;background-color:#89c997"><i class="el-icon-d-arrow-right" style="color:white"><span>纠纷已解决</span></i></div>  
                                    <div class="clear"></div>
                                    <div style="float:right;" class="studiodisputedtime1"><p><i class="el-icon-time"><i>2017-6-8 12:30</i></i></p></div> 
                                </div>
                            </el-col>
                        </el-row>
                    </el-card><br>
                </el-col>
            </el-row>
        </div>
    </div>
</template>
<script>
export default {

    data(){
        return{
            count:2,
            disputedprojects:[
                {
                    id:41,
                    name:'xiangmu1'
                },
                {
                    id:42,
                    name:'xiangmu1'
                },
                {
                    id:43,
                    name:'xiangmu1'
                },
                {
                    id:44,
                    name:'xiangmu1'
                },
                {
                    id:45,
                    name:'xiangmu1'
                }
            ]
        }
    },
    methods:{
        turntodisputeddetail:function(index){
                this.$router.push({
                path:'/studioafterlogin/disputeddetail',
                query:{
                     studio_id:this.$route.query.studio_id,               
                     disputedproject_id:this.disputedprojects[index].id
                }
            });
            
        },
       

    },
    mounted(){

    }
    
}
</script>
<style>
.studiodisputed{
   
    font-size:1.2rem;
    margin-left:5rem;
    margin-right:5rem;
    margin-top:2rem;
    text-align: left;
    background-color: rgba(0, 0, 0, 0)

}
.studiodisputedcard{
     border-color: rgba(0, 191, 255, 0);
     border-width: 0.5rem; 
}
.studiodisputedcard:hover{
     border-color: rgba(0, 191, 255, 0.3);
     border-width: 0.5rem; 
}
.studiodisputedimage {
    max-width: 100%;
    height: auto;
}
.clear{
    clear: both;
}
.studiodisputeditemname{
      font-size:30px;
    color: #4186dc;  
}
  @media(max-width: 960px) {
    .studiodisputed{
      width: 100%;
      margin: 0.5rem 0 0.5rem 0;
      font-size:15px;
      text-align: left;
    }
    .studiodisputedimage {
        max-width: 80%;
        height: auto;
        margin-top:10px;
    }
    .studiodisputeditemname{
      font-size:18px;
    margin-top:-2rem;  
    }
    .studiodisputedtime1{
        margin-bottom: -2rem;
    }
    }

</style>



