<template>
<div>
  <v-app id="inspire">


    <v-navigation-drawer
      fixed
      :clipped="$vuetify.breakpoint.mdAndUp"
      app
      v-model="drawer"
    >

        <v-list>

            <v-list-tile >
                <v-list-tile-action>
                    <v-icon>home</v-icon>
                </v-list-tile-action>
                <v-list-tile-title ><p style="font-size:22px"><b>专家个人中心</b></p></v-list-tile-title>
            </v-list-tile>

            <v-list-tile @click="turntoshouye"> 
                <v-list-tile-action>
                  <v-icon>home</v-icon>
                </v-list-tile-action>
                <v-list-tile-title>个人中心</v-list-tile-title>
            </v-list-tile>

            <template v-slot:activator>
                <v-list-tile>
                    <v-list-tile-action>
                    <v-icon>view_list</v-icon>
                    </v-list-tile-action>
                    <v-list-tile><b style="font-size:18px">仲裁调解</b></v-list-tile>
                </v-list-tile>
            </template>

            <v-list-tile @click="turntowaitting"> 
            <v-list-tile-action>
                <v-icon>refresh</v-icon>
                </v-list-tile-action>
                <v-list-tile-title>待处理纠纷</v-list-tile-title>
            </v-list-tile>

            <v-list-tile @click="turntoarbitrationhistory"> 
            <v-list-tile-action>
                <v-icon>check</v-icon>
                </v-list-tile-action>
                <v-list-tile-title>历史记录</v-list-tile-title>           
            </v-list-tile>

            <v-list-tile @click=""> 
            <v-list-tile-action>
                <v-icon>keyboard_arrow_up</v-icon>
                </v-list-tile-action>
                <v-list-tile-title>申诉上报</v-list-tile-title>
            </v-list-tile>

            <template v-slot:activator>
                <v-list-tile>
                    <v-list-tile-action>
                    <v-icon>group</v-icon>
                    </v-list-tile-action>
                    <v-list-tile><b style="font-size:18px">
                      个人管理</b></v-list-tile>
                </v-list-tile>
            </template>

            <v-list-tile @click="turntoexpertinfo"> 
            <v-list-tile-action>
                <v-icon>border_color</v-icon>
                </v-list-tile-action>
                <v-list-tile-title>我的信息</v-list-tile-title>
            </v-list-tile>

           <v-list-tile href="/"> 
            <v-list-tile-action>
                <v-icon>exit_to_app</v-icon>
                </v-list-tile-action>
                <v-list-tile-title>退出登录</v-list-tile-title>
            </v-list-tile>
        </v-list>
      


    </v-navigation-drawer>
    <v-toolbar
      color="blue darken-3"
      dark
      app
      :clipped-left="$vuetify.breakpoint.mdAndUp"
      fixed
    >
      <v-toolbar-title style="width: 300px" class="ml-0 pl-3">
        <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
        <span class="hidden-sm-and-down">天眼</span>
      </v-toolbar-title>
      <v-text-field
        flat
        solo-inverted
        prepend-icon="search"
        label="Search"
        class="hidden-sm-and-down"
      ></v-text-field>
      <v-spacer></v-spacer>
      <v-btn icon @click="turntoproject">
        <v-icon>apps</v-icon>
      </v-btn>
      <v-btn icon >
        <v-icon>notifications</v-icon>
      </v-btn>
      <v-btn icon href="/">
        <v-icon>exit_to_app</v-icon>
      </v-btn>      
      <v-btn icon large>
        <v-avatar size="32px" tile>
          <img
            src="../../assets/henry.jpg"
            alt="User"
          >
        </v-avatar>
      </v-btn>
    </v-toolbar>
    <v-content>
      <!-- <v-container fluid fill-height> -->
        <!-- <v-layout justify-center align-center> -->
             <router-view></router-view>
        <!-- </v-layout> -->
      <!-- </v-container> -->
    </v-content>
    <v-btn
      fab
      bottom
      right
      color="pink"
      dark
      fixed
      @click.stop="dialog = !dialog"
    >
      <v-icon>add</v-icon>
    </v-btn>
    <v-dialog v-model="dialog" width="800px">
      <v-card>
        <v-card-title
          class="grey lighten-4 py-4 title"
        >
          Create contact
        </v-card-title>
        <v-container grid-list-sm class="pa-4">
          <v-layout row wrap>
            <v-flex xs12 align-center justify-space-between>
              <v-layout align-center>
                <v-avatar size="40px" class="mr-3">
                  <img
                    src="//ssl.gstatic.com/s2/oz/images/sge/grey_silhouette.png"
                    alt=""
                  >
                </v-avatar>
                <v-text-field
                  placeholder="Name"
                ></v-text-field>
              </v-layout>
            </v-flex>
            <v-flex xs6>
              <v-text-field
                prepend-icon="business"
                placeholder="Company"
              ></v-text-field>
            </v-flex>
            <v-flex xs6>
              <v-text-field
                placeholder="Job title"
              ></v-text-field>
            </v-flex>
            <v-flex xs12>
              <v-text-field
                prepend-icon="mail"
                placeholder="Email"
              ></v-text-field>
            </v-flex>
            <v-flex xs12>
              <v-text-field
                type="tel"
                prepend-icon="phone"
                placeholder="(000) 000 - 0000"
                mask="phone"
              ></v-text-field>
            </v-flex>
            <v-flex xs12>
              <v-text-field
                prepend-icon="notes"
                placeholder="Notes"
              ></v-text-field>
            </v-flex>
          </v-layout>
        </v-container>
        <v-card-actions>
          <v-btn flat color="primary">More</v-btn>
          <v-spacer></v-spacer>
          <v-btn flat color="primary" @click="dialog = false">Cancel</v-btn>
          <v-btn flat @click="dialog = false">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
     <!-- <router-view></router-view> -->
</div>
</template>
<script>

  export default {
    name:'app',
    inject:['reload'],
    data() {
      return {
           expert_id:this.$route.query.expert_id,
           id:this.$route.query.company_id,
           active: false,
           show:-1,
           content:null,
           dialog: false,
           drawer:null,

      };
    },
    components:{
    
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
      turntologin:function(){
          this.$router.push('/login');
          this.$router.go(0);
         
      },
       turntoshouye:function(){
          this.$router.push({
                path:'/expertafterlogin/shouye',
                query:{
                     expert_id:this.$route.query.expert_id,
                }
            });
            this.$router.go(0);
      },
      turntowaitting:function(){
         this.$router.push({
                path:'/expertafterlogin/waitting',
                query:{
                     expert_id:this.$route.query.expert_id,
                }
            });
          this.$router.go(0);
      },
       turntoarbitrationhistory:function(){
          this.$router.push({
                path:'/expertafterlogin/arbitrationhistory',
                query:{
                     expert_id:this.$route.query.expert_id,
                }
            });
            this.$router.go(0);
      },
      turntoexpertinfo:function(){
          this.$router.push({
                path:'/expertafterlogin/expertinfo',
                query:{
                     expert_id:this.$route.query.expert_id,
                }
            });
            this.$router.go(0);
      },
    
    }
  }
 
 
</script>
<style>
#nav1{
    z-index:3;
    position:fixed;
    top:0;
    width:100%;
    opacity:1;
    background-color: blue;
}
.el-button {
    margin-left:20px;
    font-size:18px;
    box-sizing: border-box;
  }
.el-button:hover{
    color:chartreuse;
  }
  
  
</style>



